/* ==================== common styles ======================= */
html {
  font-size: 14px;
  /*used for rem-units*/
}

body {
  min-width: 1300px;
  max-width: 100vw;
  overflow-x: hidden;
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 18px;
  color: #fff;
  line-height: 1.2;
  background-color: #4C554E;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 10;
}

.overlay.transparent {
  background: transparent;
}

.flex {
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

a,
button {
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}

img {
  outline: none;
  border: none;
  max-width: 100%;
}

input,
input:focus,
textarea {
  outline: none;
}

::-webkit-input-placeholder {
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 23px;
  color: #81cc00;
}

::-moz-placeholder {
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 23px;
  color: #81cc00;
}

:-ms-input-placeholder {
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 23px;
  color: #81cc00;
}

::placeholder {
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 23px;
  color: #81cc00;
}

b,
strong {
  font-family: "CodePro-Bold", sans-serif;
}

section {
  position: relative;
}

.wrapper {
  width: 96%;
  max-width: 1920px;
  margin: 0 auto;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  z-index: 9;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  display: block;
  min-width: 78px;
  width: 78px;
  margin-right: 5px;
}

.header-nav-list {
  white-space: nowrap;
  height: 46px;
  line-height: 46px;
}

.header-nav-list li {
  display: inline-block;
}

.header-nav-list a {
  text-transform: uppercase;
  font-family: "PFDinTextCondPro-Regular", sans-serif;
  font-size: 18px;
  color: #fff;
  padding: 0 12px;
  transition: all .4s;
}

.header-nav-list a:hover, .header-nav-list a.active {
  color: #FBA01E;
}

.header-social {
  white-space: nowrap;
}

.header-social li {
  display: inline-block;
  margin-right: 8px;
}

.header-social li:last-child {
  margin-right: 0;
}

.header-social a {
  display: block;
}

.header-social a img {
  filter: grayscale(100%);
  transition: all .4s;
}

.header-social a:hover img {
  filter: grayscale(0);
}

.call-btn {
  cursor: pointer;
}

