.footer-logo {
  display: block;
  width: 80px;
  margin-right: 6px;
}

.footer-logo-cont {
  display: flex;
  align-items: flex-end;
}

.footer-logo-cont p {
  font-size: 14px;
  font-weight: 600;
  color: #C9C9C9;
}

.footer-logo-cont .copy {
  font-weight: 400;
  color: #939393;
}

.footer-mail-block {
  text-align: center;
  font-size: 14px;
}

.footer-mail-block p {
  color: #A5C62D;
}

.footer-mail-block a {
  letter-spacing: 2px;
  font-weight: 700;
  border-bottom: 1px solid #A5C62D;
  transition: all .4s;
}

.footer-mail-block a:hover {
  border-bottom-color: #FF000C;
}

.footer-develop {
  font-size: 14px;
  color: #939393;
  text-align: right;
}

.footer-develop a {
  text-transform: uppercase;
  color: #A5C62D;
  border-bottom: 1px solid transparent;
  transition: all .4s;
}

.footer-develop a:hover {
  border-bottom-color: #A5C62D;
}

.cookies-notice {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  max-height: 70vh;
  padding: 40px 0 50px;
  font-family: "PFDinTextCondPro-Medium", sans-serif;
  font-size: 18px;
  line-height: 1.2;
  color: #fff;
  background-color: #000;
  z-index: 9;
}

.cookies-notice .wrapper {
  max-width: 960px;
}

.cookies-notice .notice-head {
  display: flex;
  justify-content: space-between;
  font-family: "CodePro-Bold", sans-serif;
  font-size: 24px;
  color: #B5E200;
  margin-bottom: 12px;
}

.notice-close {
  cursor: pointer;
}

.modal-call {
  cursor: pointer;
}

.modal-cont {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 11;
}

.modal-cont:before {
  content: "";
  display: block;
  position: absolute;
  top: 40px;
  bottom: 0;
  right: 60px;
  left: 0;
  background-image: url(../img/geometry/red.png);
  background-repeat: no-repeat;
  background-size: 70% 90%;
  background-position: top right;
  transform: rotate(-10deg);
  z-index: -1;
}

.modal-cont .call-form-cont {
  width: 1100px;
  max-width: 90%;
  padding: 40px 10px 120px 80px;
}

.modal-cont .call-form-subheading {
  text-transform: initial;
}

.modal-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 800px;
}

.modal-close {
  font-family: "CodePro-Bold", sans-serif;
  font-size: 48px;
  color: #B5E200;
  cursor: pointer;
  text-shadow: 0px 3px 2px rgba(0, 0, 0, 0.4), 0px 6px 10px rgba(0, 0, 0, 0.1), 0px 13px 16px rgba(0, 0, 0, 0.1);
}

/* ==================== css-animations ======================= */
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes pulse-sm {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}

